<template>
<div id="list">
  <ul v-for="item in list">
    <li>
      <circle-list-item :childlist="item" @showinput="show"></circle-list-item>
    </li>
  </ul>
  <div v-show="visible" ref="commentinput" style="position: fixed;bottom: -0.35rem;width: 100%;border-top: 1px solid #ddd;">
    <yd-cell-group>
      <yd-cell-item>
        <input type="text" slot="right" placeholder="请输入内容">
        <yd-sendcode slot="right" type="primary" init-str="发送"></yd-sendcode>
      </yd-cell-item>
    </yd-cell-group>
  </div>
</div>
</template>

<script>
import CircleListItem from './CircleListItem'
export default {
  name: 'CircleList',
  components: {
    CircleListItem
  },
  data() {
    return {
      list: [1, 2],
      visible: false
    }
  },
  methods: {
    togglePanel() {
      this.visible ? this.hide() : this.show()
    },
    show() {
      this.visible = true
      document.addEventListener('click', this.hidePanel, true)
    },

    hide() {
      this.visible = false
      document.removeEventListener('click', this.hidePanel, false)
    },
    hidePanel(e) {
      if (!this.$refs.commentinput.contains(e.target)) {
        this.hide()
      }
    }
  }
}
</script>



