import Vue from 'vue'
import Router from 'vue-router'
import PetVariety from '@/views/Variety/VarietyList'
import Register from '@/views/Register'
import Circle from '@/views/Circle/CircleList'
import YDUI from 'vue-ydui';
import 'vue-ydui/dist/ydui.rem.css';

Vue.use(Router)
Vue.use(YDUI)

export default new Router({
  routes: [{
      path: '/PetVariety',
      name: 'PetVariety',
      component: PetVariety
    },
    {
      path: '/Register',
      name: 'Register',
      component: Register
    },
    {
      path: '/Circle',
      name: 'Circle',
      component: Circle
    },
    {
      path: '/',
      name: 'Circle',
      component: Circle
    }
  ]
})



