<template>
<div>
  <div class="po-avt-wrap">
    <img class="po-avt data-avt" src="../../../static/touxiang.jpg" />
  </div>
  <div class="po-cmt">
    <div class="po-hd">
      <p class="po-name">
        <span class="data-name">小小小强~</span>
        <span class="focus-btn">
          <img src="../../../static/add.png" style="width:10px;height:10px;position:relative;top:2px;">关注
        </span>
      </p>
      <div class="post">
        <p>大家好，听说国内冻成狗🐶？我这边还挺热～</p>
        <p>
          <img class="list-img" src="../../../static/circle1.jpg" style="height: 80px;" />
          <img class="list-img" src="../../../static/circle2.jpg" style="height: 80px;" />
          <img class="list-img data-avt" src="../../../static/circle3.jpg" style="height: 80px;" />
        </p>
      </div>
      <div ref="commentsmain">
        <p class="time">刚刚</p><img class="c-icon" src="../../../static/comment-btn.png" @click="togglePanel" />
        <div class="comments" v-show="visible">
          <img class="goodimg" src="../../../static/good.png" @click="good" />
          <span style="float:left;color:#777;font-size:14px;">|</span>
          <img class="commentsimg" src="../../../static/comments.png" @click="comment" />
        </div>
      </div>
    </div>
    <div class="r"></div>
    <div class="cmt-wrap">
      <div class="like"><img src="../../../static/good-ing.png" />苍井空，陈冠希，杨幂，王思聪，陈赫，刘德华，马云...</div>
      <div class="cmt-list">
        <p><span>wu世勋-EXO：</span>나는 서명～</p>
        <p><span>鹿晗：</span>我们在国内冻成狗，我也想跟哥您去热热～</p>
        <p><span>权志龙：</span>나는 서명～</p>
        <p><span>王思聪：</span>去哪玩啊？那么爽</p>
        <p>
          <span class="data-name">万虎科技~贾素杰</span> 回复
          <span>王思聪</span>
          <span>：</span> 澳洲大堡礁，这边刚好是夏季，挺适合避寒的。
        </p>
        <p><span>杨幂：</span>😘私人飞机出行，求带上我～</p>
      </div>
    </div>
  </div>
</div>
</template>


<script>
export default {
  props: ['childlist'],
  name: 'CircleListItem',
  data() {
    return {
      list: [],
      visible: false
    }
  },
  methods: {
    togglePanel() {
      this.visible ? this.hide() : this.show()
    },
    show() {
      this.visible = true
      document.addEventListener('click', this.hidePanel, false)
    },

    hide() {
      this.visible = false
      document.removeEventListener('click', this.hidePanel, false)
    },
    hidePanel(e) {
      if (!this.$refs.commentsmain.contains(e.target)) {
        this.hide()
      }
    },
    /**
     * 点赞
     */
    good() {
      this.$dialog.toast({
        mes: '点赞!',
        timeout: 1500
      });
      this.visible = false;
    },
    /**
     * 评论
     */
    comment() {
      this.visible = false;
      this.$emit('showinput');
    }

  },
  beforeDestroy() {
    this.hide()
  }
}
</script>

<style>
#list,
#list li,
.po-hd,
.post {
  overflow: hidden;
}

.po-hd {
  padding-bottom: 5px;
}

.po-cmt,
.post .list-img:nth-child(1),
.time {
  float: left;
}

#list li,
.cmt-wrap,
.r,
.time {
  clear: both;
}

ol,
ul {
  list-style: none none;
}

@media screen and (min-width:350px) {
  html {
    font-size: 15px;
  }

  .cmt-wrap {
    font-size: 14px;
  }

  .time {
    font-size: 13px;
  }
}

header {
  position: relative;
}

#list li,
.po-hd {
  position: relative;
}

#list {
  padding: 30px 0 10px;
}

#list li {
  line-height: 1.5;
  border-bottom: 1px solid #e2e2e2;
  margin-top: 15px;
  padding-bottom: 15px;
}

.po-avt {
  position: absolute;
}

.po-avt-wrap {
  padding-left: 10px;
}

.po-avt {
  width: 40px;
  height: 40px;
  top: 0;
  left: 10px;
}

.r {
  border-bottom: 8px solid #eee;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  width: 1px;
  /* margin-top: 5px; */
  margin-left: 10px
}

.po-cmt {
  padding-left: 60px;
  padding-right: 10px;
  width: 100%;
  box-sizing: border-box;
  text-align: left;
}

.po-name {
  color: #576b95;
  font-size: 15px;
}

.post {
  color: #252525;
  padding-bottom: 10px;
  font-size: 15px;
  margin-top: 5px;
}

.post img {
  padding: 10px 5px 0 0;
  display: block;
  max-height: 130px;
  max-width: 130px;
}

.post .list-img {
  width: 30%;
  max-height: 80px;
  max-width: 80px;
  padding-right: 5px;
  float: left;
  object-fit: cover;
}

.post .list-img:last-child {
  padding-right: 0;
}

.time {
  color: #b1b1b1;
}

.c-icon {
  width: 20px;
  float: right;
}

.cmt-wrap {
  width: 100%;
  background-color: #eee;
}

.like {
  color: #576b95;
  padding: 5px 5px 3px 12px;
}

.like img {
  width: 20px;
  padding-right: 5px;
}

.cmt-list {
  padding: 5px 12px;
  color: #454545;
}

.cmt-list p {
  padding-top: 3px;
}

.cmt-list span {
  color: #3b5384;
}

.focus-btn {
  border: 1px solid #BB6804;
  border-radius: 3px;
  padding: 3px 5px;
  font-size: 10px;
  position: absolute;
  right: 0;
  color: #BB6804;
}

.comments {
  position: absolute;
  background: #666;
  padding: 5px;
  right: 25px;
  border-radius: 3px;
  bottom: 0px;
}

.comments .goodimg {
  width: 20px;
  height: 20px;
  padding: 3px;
  margin: 0 15px;
  float: left;
}

.comments .commentsimg {
  width: 20px;
  height: 20px;
  padding: 3px;
  margin: 0 15px;
  float: left;
}
</style>



