<template>
<div class="">
  <div class="yd-grids-2">
    <a v-for="item in list" class="yd-grids-item router-link-exact-active router-link-active" style="height: 3.8rem;">
      <list-item :childlist="item"></list-item>
    </a>
  </div>

</div>
</template>

<script>
import ListItem from './VarietyListItem'
//import mockdata from "../../../mock/mocktest";
export default {
  name: 'VarietyList',
  components: {
    ListItem
  },
  data() {
    return {
      list: [{
          "img":"../../../static/taidi.png",
          "variety": "泰迪",
          "popularity": "1234"
        },
        {
          "img":"../../../static/bixiong.png",
          "variety": "比熊",
          "popularity": "4321"
        },
        {
          "img":"../../../static/bomei.png",
          "variety": "博美",
          "popularity": "2413"
        }
      ]
    }
  }
}
</script>



