<template>
<div class="hello">
  <div>
    <img :src="list.img" style="width: 100%;">
  </div>
  <div class="yd-grids-txt">
    <span>萌宠品种：{{list.variety}}</span>
  </div>
  <div class="yd-grids-txt">
    <span>人气：{{list.popularity}}</span>
  </div>
</div>
</template>


<script>
export default {
  props: ['childlist'],
  name: 'VarietyListItem',
  data() {
    return {
      list: this.childlist
    }
  }
}
</script>



